import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import "./scrollNavbar.css";
import DndNavbar from "./DndCategories";
import { useHistory, useLocation } from "react-router-dom";
import {
  Collapse,
  NavbarBrand,
  Navbar,
  Container,
  UncontrolledTooltip,
} from "reactstrap";

import { connect, useSelector } from 'react-redux';
import { actions } from '../../redux/action';
import ModalExample from "../reactstrapComponents/modal";

function ScrollTransparentNavbar(props) {
  const show = useSelector(state => state.productReducer.displayClientConfigurator);
useEffect(() => {
  document.getElementById("store_main_navbar").scrollIntoView({ block: "end", behavior: 'smooth' });
 
}, [])

  const [activeHome, setActiveHome] = useState("");
  const location = useLocation();
  const [activeCategory, setActiveCategory] = useState("");
  const history = useHistory();
  const [modal, setModal] = useState(false);

  useEffect(() => {

    let splitedPathname = location.pathname.split("/");
    if (splitedPathname[1] === props.storeName && splitedPathname[2] === undefined)
      setActiveHome("Home");
    else
      setActiveHome("");

    if (splitedPathname[2] === "category")
      setActiveCategory(splitedPathname[3]);
    else
      setActiveCategory("");
  })

  function searchObj(obj, searchText) {
    for (let key in obj) {
      let value = obj[key];
      let s;
      if (typeof value === 'object') {
        s = searchObj(value, searchText);
      }
      else {
        try {
          value = `${value}`;
        }
        finally {
          if ((typeof value === 'string' && value.toLowerCase().indexOf(searchText) > -1) || s) {
            return obj;
          }
        }
      }
    }
  }


  let searchPromise;
  let textValue

  async function asyncSearchFunction(elementRefValue) {
    textValue = elementRefValue;
    await (
      searchPromise = new Promise((resolve, reject) => {
        let searchFilter = props.products.filter(obj => {
          return searchObj(obj, textValue);
        });

        resolve(searchFilter)
      })
    )


    searchPromise.then(async (msg) => {
      console.log("Promise resolved");
      console.log(msg);
      await props.setFilteredProducts(msg);
    }, () => {
      console.log("Promise reject");
    })
  }

  function openMediaGallery() {
    props.setfunctionToSetImage("setStoreLogo")
    props.setImageLocation(location.pathname)
    history.push("/" + props.objectFields.urlRoute + "/mediaGallery/uploudImage");
  }

  return (
    <>
      <Navbar id="store_main_navbar" className="ST-headerStore" expand="lg" style={{ width: show == false ? "92.5%" : props.mainWidth, backgroundColor: props.navbarBackground != "" ? props.navbarBackground : "white" }}
        onClick={() => {
          props.changeCurrentComponent("HomeConfigurator");
          props.setCollapse("menu");
        }}
      >
        <Container className="d-flex justify-content-between">
          <NavbarBrand to={"/" + (props.objectFields.urlRoute ? props.objectFields.urlRoute : props.objectFields.storeName)} tag={Link} id="navbar-brand">
            <img alt="..."
              src={props.objectFields.logo} className="logoHeader"
              onClick={(e) => {
                openMediaGallery()
                e.preventDefault()
              }}
            >
            </img>
          </NavbarBrand>
          <UncontrolledTooltip target="navbar-brand">
            To Home Store Page
            </UncontrolledTooltip>
          <Collapse
            className="d-flex justify-content-between"
            navbar>
            <Link
              to={`/${props.storeName}`}
              style={{ color: (activeHome === "Home" ? props.activeTextColor : props.textColor), fontWeight: 900 }}
              onClick={(e) => {
                e.stopPropagation()
                props.setComponent("HomeConfigurator")
              }}>
              Home
            </Link>
            {!props.isAdmin

              ? props.categories.filter(item => {
                if (!item.masterCategory && item.masterCategory === null)
                  return item
              }).map((item, index) => (
                // <div onClick={() => {
                //   props.setCurrentCategory(item);
                //   // e.preventDefault();
                //   // e.stopPropagation()
                // }}>
                <Link key={index} to={`/${props.storeName}/category/${item.categoryName}`}
                  onClick={() => props.setCurrentCategory({ currentCategory: item, index: index })}

                  style={{ color: (activeCategory === item.categoryName ? props.activeTextColor : props.textColor), backgroundColor: "transparent" }}>
                  {item.categoryName}
                </Link>
                // </div>
              )) :
              <DndNavbar activeHome={activeHome} setActiveHome={setActiveHome} activeCategory={activeCategory} setActiveCategory={setActiveCategory}></DndNavbar>}


            {/* <Nav className="mx-auto" id="ceva" navbar>
                {props.categories.filter(item => {
                  if (!item.masterCategory && item.masterCategory === null)
                  return item
                }).map((item, index) => (
                  
                  <UncontrolledDropdown nav key={index}>
                  
                  <DropdownToggle
                  caret
                  color="default"
                  data-toggle="dropdown"
                  href="#pablo"
                  id="navbarDropdownMenuLink"
                  nav
                  onClick={(e) => e.preventDefault()}
                  >
                  <p>{item.categoryName}</p>
                  </DropdownToggle>
                  
                  <DropdownMenu aria-labelledby="navbarDropdownMenuLink" right>
                  {console.log("iiiitem", item)}
                  {item.childrenCategory.map((child, index) => (
                    <DropdownItem key={index} to="/sections#contact-us" tag={Link}>
                    {/* <i className="now-ui-icons tech_mobile"></i> */}
            {/* {child.categoryName}
                    </DropdownItem>
                    ))}
                    </DropdownMenu>
                    </UncontrolledDropdown>
                    
                    ))}
                    </Nav>
                  */}
            <div className="row">

              <div className="d-flex justify-content-between">
                <div className="col">
                  <div style={{ float: "left", cursor: "pointer", width: '30px', height: '1rem !important' }} onClick={setModal}>
                    <FontAwesomeIcon className="fas fa-lg" icon={['fas', 'search']} ></FontAwesomeIcon>
                  </div>
                </div>
                <div className="col">
                  <div className="separatorStripe" style={{ float: "right", }}></div>
                </div>
              </div>
              <div className="col">
                <Link to={"/" + (props.objectFields.urlRoute ? props.objectFields.urlRoute : props.objectFields.storeName) + "/cart"} style={{ float: "left" }}>
                  <>

                    <FontAwesomeIcon className="fas fa-lg" color="#2c2c2c" id="tooltip7192" icon={['fas', 'shopping-cart']}
                      onMouseEnter={props.cartProducts.length > 0 && (() => props.cartPanal_open())}
                    >

                    </FontAwesomeIcon>
                    {props.cartProducts.length === 0 &&
                      <UncontrolledTooltip
                        delay={0}
                        placement="bottom"
                        target="tooltip7192"
                      >

                        The Cart Is Empty!!!!
                    </UncontrolledTooltip>
                    }

                  </>
                  {props.cartProducts &&
                    <span className="badge rounded-pill badge-notification" style={{ backgroundColor: "#FC894D" }}>{props.cartProducts.length}</span>
                  }
                </Link>
              </div>

            </div>
          </Collapse>
          {/* </div> */}
        </Container>
      </Navbar>
      {/* <Modal isOpen={modal} toggle={toggle} className="jijij">
                  <ModalHeader toggle={toggle} close={closeBtn}>Modal title</ModalHeader>
                  <ModalBody>
                  <Input />
                  </ModalBody>
                  <ModalFooter>
                  <Button color="primary" onClick={toggle}>Search</Button>{' '}
                  </ModalFooter>
                  </Modal>
                */}
      <ModalExample modal={modal} setModal={setModal} asyncSearchFunction={asyncSearchFunction}></ModalExample>
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    cartProducts: state.cartReducer.cart.products,
    objectFields: state.storeReducer.objectFields,
    products: state.productReducer.products,
    mainWidth: state.wrapReducer.mainWidth,
    collapseOfRedux: state.bullPageEditReducer.collapse,
    backgroundMenu: state.bullPageEditReducer.backgroundMenu,
    categories: state.categoriesReducer.categoryListMenu,
    storeName: state.storeReducer.objectFields.urlRoute ?
      state.storeReducer.objectFields.urlRoute :
      state.storeReducer.objectFields.storeName,

    isAdmin: state.viewOrEditReducer.isAdmin,
    textColor: state.storeNavbarReducer.storeNavbarDesign.textColor,
    activeTextColor: state.storeNavbarReducer.storeNavbarDesign.activeTextColor,
    navbarBackground: state.storeNavbarReducer.storeNavbarDesign.backgroundColor

  }
}
const mapDispatchToProps = (dispatch) => ({
  setChangeImgInCurrentLocation: (location) => dispatch(actions.setChangeImgInCurrentLocation(location)),
  setfunctionToSetImage: (location) => dispatch(actions.setfunctionToSetImage(location)),
  setImageLocation: (location) => dispatch(actions.setImageLocation(location)),
  setFilteredProducts: (p) => dispatch(actions.setFilteredItems(p)),
  changeCurrentComponent: (e) => dispatch(actions.setCurrentComponent(e)),
  setCollapse: (collapseOfRedux) => { dispatch(actions.setCollapse(collapseOfRedux)) },
  setCurrentCategory: (c) => { dispatch(actions.setCurrentCategory(c)) },
  setComponent: (component) => { dispatch(actions.setCurrentComponent(component)) },
  setNavbarBackgroundColor: (x) => { dispatch(actions.setNavbarBackgroundColor(x)) }

})
export default connect(mapStateToProps, mapDispatchToProps)(ScrollTransparentNavbar)